<template>
  <div>
    <va-card class="py-0 mb-0" :title="$t('overview.news.relatedNews')" style="box-shadow: none;"></va-card>
    <div class="collapse-page">
      <div class="row px-2">
        <template v-for="(industry, i) in allNews">
          <div
            :class="{
              'flex px-1 pb-1': true,
              'xs12': i == 0,
              'lg12 xl6 pt-0': i > 0,
            }"
            :key="'card' + industry.industry_name"
          >
            <va-card class="news-card mb-1" stripe="moccasin">
              <div
                class="display-5 py-3 px-3 ma-0 industry-title"
                :key="'industry name: ' + industry.industry_name"
              >
                {{ industry.industry_name }}
              </div>

              <va-accordion class="px-3" :key="'accordion' + industry.industry_name">
                <va-collapse
                  v-for="(news, field) in industry.news"
                  :key="'va-item' + field"
                  class="ma-0"
                >
                  <span
                    class="va-item px-0 align--center va-item--clickable"
                    slot="header"
                    style="background: none;"
                  >
                    {{ news.title }}
                  </span>
                  <div slot="body" class="text-block" style="text-align: justify;">
                    {{ news.content }}
                  </div>
                </va-collapse>
              </va-accordion>
            </va-card>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import allNews from '../../../data/overview_news.json'

export default {
  name: 'collapses',
  data () {
    return {
      allNews: allNews.slice(),
    }
  },
}
</script>

<style lang="scss">
.va-collapse {
  &__body {
    padding-top: 0 !important;
    padding-left: 0;
    padding-right: 0;
    margin-top: 0 !important;
  }

  &__header__content {
    background: none;
    box-shadow: none;
    padding: 0;
  }
}

.industry-title {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: moccasin;
  color: firebrick;
}

.va-card.news-card > .va-card {
  &__body {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
  }
}
</style>
