<template>
  <va-data-table
    :fields="fields"
    :data="starredIndustries"
    class="va-table--hoverable"
    no-pagination
    clickable
    @row-clicked="redirect"
  >
    <template slot="trend" slot-scope="props">
      <va-icon :name="getTrendIcon(props.rowData.chgPct)" :color="getTrendColor(props.rowData.chgPct)" />
    </template>

    <template slot="chgPct" slot-scope="props">
      <div :style="'color:' + getChgPctColor(props.rowData.chgPct)">
        {{ getChgPct(props.rowData.chgPct) }}
      </div>
    </template>

    <template slot="starred" slot-scope="props">
      <va-button
        flat small
        :color="getStarColor(props.rowData)"
        icon="fa fa-star"
        @click.stop="star(props.rowData)"
      />
    </template>
  </va-data-table>
</template>

<script>
import industries from '../../../../data/overview_industries.json'

export default {
  data () {
    return {
      industries: industries,
    }
  },
  computed: {
    starredIndustries () {
      return this.industries.filter(industry => industry.starred === true)
    },
    fields () {
      return [
        {
          name: '__slot:trend',
          width: '5%',
          dataClass: 'text-center',
        },
        {
          name: 'industryName',
          title: this.$t('overview.industryDetails.headings.industry'),
          width: '60%',
        }, {
          name: '__slot:chgPct',
          title: this.$t('overview.industryDetails.headings.chgPct'),
          sortField: 'chgPct',
          width: '15%',
        }, {
          name: 'turnover',
          title: this.$t('overview.industryDetails.headings.turnover'),
          sortField: 'turnover',
          width: '15%',
          sortable: true,
        }, {
          name: 'peRatio',
          title: this.$t('overview.industryDetails.headings.peRatio'),
          sortField: 'peRatio',
          width: '5%',
        }, {
          name: '__slot:starred',
        },
      ]
    },
  },
  methods: {
    getTrendIcon (chgPct) {
      if (chgPct > 0) {
        return 'fa fa-caret-up'
      }

      if (chgPct < 0) {
        return 'fa fa-caret-down'
      }

      return 'fa fa-minus'
    },
    getTrendColor (chgPct) {
      if (chgPct > 0) {
        return 'primary'
      }

      if (chgPct < 0) {
        return 'danger'
      }

      return 'grey'
    },
    getChgPct (chgPct) {
      if (chgPct > 0) {
        return '+' + chgPct + '%'
      }

      return chgPct + '%'
    },
    getChgPctColor (chgPct) {
      if (chgPct > 0) {
        // green
        return 'rgb(64, 229, 131)'
      }

      if (chgPct < 0) {
        // red
        return 'rgb(227, 75, 74)'
      }

      return 'grey'
    },
    getStarColor (user) {
      return user.starred ? 'primary' : 'gray'
    },
    star ({ industryCode }) {
      const i = this.industries.findIndex(industry => industry.industryCode === industryCode)
      this.industries[i].starred = !this.industries[i].starred

      const industryName = this.industries[i].industryName

      var toastMsg = this.industries[i].starred
        ? (this.$t('overview.starringToastMsg.add.pre') + industryName + this.$t('overview.starringToastMsg.add.post'))
        : (this.$t('overview.starringToastMsg.remove.pre') + industryName + this.$t('overview.starringToastMsg.remove.post'))
      this.showToast(toastMsg, {
        icon: 'fa-check',
        position: 'bottom-right',
        duration: 1500,
      })
    },
    redirect (industryInfo) {
      var industryName = industryInfo.industryName
      var industryCode = industryInfo.industryCode

      this.$store
        .dispatch('setIndustryAPI', {
          industryName,
          industryCode,
        })
        .then(() => {
          this.$router.push({
            name: 'industry-overview',
          })
        })
        .catch(err => {
          console.log(err)
          this.snackbar = true
        })
    },
  },
}
</script>

